import React, { Component } from 'react'

export class SupAdminHis extends Component {
    render() {
        return (
            <div>

               
  <div className="content-body">
  <div className="container-fluid">
    <div className="row">
      <div className="col-xl-12">
        <div className="card">
          <div className="card-header border-0">
            <h4 className="card-title">History</h4>
          </div>
          <div className="card-body pt-0">
            <div className="transaction-table">
              <div className="table-responsive">
                <table className="table mb-0 table-responsive-sm">
                  <tbody>
                <tr>
                  <td><span className="sold-thumb"><i className="la la-arrow-down" /></span>
                  </td>
                  <td>
                    <span className="badge badge-danger">Selling Rate</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td className="text-danger">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>
                <tr>
                  <td><span className="buy-thumb"><i className="la la-arrow-up" /></span>
                  </td>
                  <td>
                    <span className="badge badge-success">Buying Rate</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td className="text-success">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>
              </tbody>

                 
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>



            </div>
        )
    }
}

export default SupAdminHis
