import axios from "axios";
//import authHeader from './auth-header';

const API_URL = "https://cryptong.herokuapp.com/api/";


class AuthService {
  login(email, password) {
    return axios
      .post(API_URL + "login", {
        email,
        password
      })
      .then(response => {
        console.log(response.data);
        if (response.data) {         
           localStorage.setItem("access_token", JSON.stringify(response.data));
        }

        return response.data;
       
      });
  }

  logout() {
    localStorage.removeItem("users");
    localStorage.removeItem("access_token");
  }
 
  register(first_name,last_name, email,phone, password,c_password) {
    return axios.post(API_URL + "register", {
     first_name,
     last_name,
      email,
      phone,
      password,
      c_password
    });
  }

  getCurrentUser() {   
    return JSON.parse(localStorage.getItem('access_token'));
  }

  getProfile(){
 // const headers = authHeader();
  const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
  const headers = { Authorization: 'Bearer ' + token };
  //console.log(headers);
  return axios.get(API_URL + 'profile', { headers: headers  }).then((res)=>{
    localStorage.setItem("users", JSON.stringify(res.data));
    return true;
  }).catch(err=>{
    //console.log(err);
    return false;
  });
  }

  getUsers(){
    // const headers = authHeader();
     const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
     const headers = { Authorization: 'Bearer ' + token };
     //console.log(headers);
     return axios.get(API_URL + 'admin/users', { headers: headers  }).then((res)=>{
      //  localStorage.setItem("users", JSON.stringify(res.data));
       return res.data;
     }).catch(err=>{
       //console.log(err);
       return false;
     });
     }
}

export default new AuthService();