import React, { Component } from 'react';
import { HashRouter, Switch } from 'react-router-dom'
import AppRoute from './RouteConfg/AppRoute';
import Login from './pages/Login';
import AdminLayout from './Layout/Admin/AdminLayout';
import AdminPage   from './Admin/UserList';
import Loginlayout from './Layout/User/Loginlayout';
import SupAdminLayout from './Layout/SupAdmin/SupAdminLayout';
import SupAdminPage from './SuperAdmin/SupAdminPage';
import SupAdminHis from './SuperAdmin/SupAdminHis';
import UsersList from './SuperAdmin/Users'
import NewUser from './SuperAdmin/NewUser';
import HomePage from './Admin/HomePage';
import Transaction from './SuperAdmin/Transaction';
import UserTrans from './Admin/Transaction';

class App extends Component {

  render() {

  return (
    
    <HashRouter basename={process.env.PUBLIC_URL}>
    <Switch>
       <AppRoute exact  path="/" component={Login} layout={Loginlayout}/>
        {/* Dashboard Links */}
        <AppRoute path="/adminUser"  component={AdminPage} layout={AdminLayout} />
        <AppRoute path="/admin"  component={HomePage} layout={AdminLayout} />
        <AppRoute path="/userTrans"  component={UserTrans} layout={AdminLayout} />
        <AppRoute path="/supAdmin"  component={SupAdminPage} layout={SupAdminLayout} />
        <AppRoute path="/supAdminHis"  component={SupAdminHis} layout={SupAdminLayout} />
        <AppRoute path="/usersList"  component={UsersList} layout={SupAdminLayout} />
        <AppRoute path="/createUser"  component={NewUser} layout={SupAdminLayout} />
        <AppRoute path="/trans"  component={Transaction} layout={SupAdminLayout} />

       
    </Switch>
    </HashRouter>
    
    
    
  );
}
}

export default App;
