import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Sidebar extends Component {
    render() {
        return (
            <div>
<div className="sidebar">
  <div className="menu">
    <ul>
    <li>
        <Link to="/admin" data-toggle="tooltip" data-placement="right" title="Home">
          <span><i className="la la-home" /></span>
        </Link>
      </li>
      <li>
        <Link to="/userTrans" data-toggle="tooltip" data-placement="right" title="Transactions">
          <span><i className="la la-receipt" /></span>
        </Link>
      </li>
      <li>
        <Link to="/adminUser" data-toggle="tooltip" data-placement="right" title="Users">
          <span><i className="la la-users" /></span>
        </Link>
      </li>
    </ul>
  </div>
</div>

            </div>
        )
    }
}

export default Sidebar
