import React, { Component } from 'react'
import { Link } from 'react-router-dom'


export class DashboardHeader extends Component {
   //constructor Code
   constructor(props) {
    super(props);
    const userProfile = JSON.parse((localStorage.getItem('users'))).data;  
    this.state = {
      user:userProfile
    }
  }
    render() {
      const {user} = this.state;
        return (
            <div>
<div className="header dashboard">
  <div className="container-fluid">
    <div className="row">
      <div className="col-xl-12">
        <nav className="navbar navbar-expand-lg navbar-light px-0 justify-content-between">
        <Link className="navbar-brand" to="/"><img src="assets/images/blogo.jpg" alt=""/></Link>

          <div className="dashboard_log my-2">
            <div className="d-flex align-items-center">
              <div className="account_money">
                <ul>
                  <li className="crypto">
                    <span>0.0025</span>
                    <i className="cc BTC" />
                  </li>
                  <li className="usd">
                    <span>19.93 USD</span>
                  </li>
                </ul>
              </div>
              <div className="profile_log dropdown">
                <div className="user" data-toggle="dropdown">
                  <span className="thumb"><i className="la la-user" /></span>
                  <span className="name">{user.first_name} {user.last_name}</span>
                  <span className="arrow"><i className="la la-angle-down" /></span>
                </div>
                <div className="dropdown-menu dropdown-menu-right">
                  <Link to="/" className="dropdown-item logout">
                    <i className="la la-sign-out" /> Logout
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </nav>
      </div>
    </div>
  </div>
</div>
</div>
        )
    }
}

export default DashboardHeader
