import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class SupAdminPage extends Component {
    render() {
        return (
            <div>





 <div className="content-body">
<div className="container-fluid">

<div className="row">
  <div className="col-xl-3 col-lg-4 col-xxl-4">
    <div className="card">
      <div className="card-header border-0 py-0">
        <h4 className="card-title">Set Rates</h4>
      </div>
      <div className="card-body">
        <div className="buy-sell-widget">
          <form name="myform" className="currency_validate">
            <div className="form-group">
              <label className="mr-sm-2">Set Buying Rate</label>
              <div className="input-group mb-3">
              <label style={{padding: "15px",
               fontSize: "18px"}} name="bitcoin" className="form-control"><b>Bitcoin</b></label>
                <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
              </div>
            </div>
            <div className="form-group">
              <label className="mr-sm-2">Set Selling Rate</label>
              <div className="input-group mb-3">
              <label style={{padding: "15px",
               fontSize: "18px"}} name="bitcoin" className="form-control"><b>Bitcoin</b></label>
                <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
              </div>
            </div>         
            <button type="submit" name="submit" className="btn btn-success btn-block">Update Rates</button>
          </form>
        </div>
      </div>
    </div>
  </div>
  <div className="col-xl-9 col-lg-8 col-xxl-8">
    <div className="card">
      <div className="card-header border-0 py-0">
        <h4 className="card-title">Recent Activities</h4>
       
      </div>
      <div className="card-body">
        <div className="transaction-table">
          <div className="">
            <table className="table mb-0 table-responsive-sm">
              <tbody>
                <tr>
                  <td><span className="sold-thumb"><i className="la la-arrow-down" /></span>
                  </td>
                  <td>
                    <span className="badge badge-danger">Selling Rate</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td className="text-danger">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>
                <tr>
                  <td><span className="buy-thumb"><i className="la la-arrow-up" /></span>
                  </td>
                  <td>
                    <span className="badge badge-success">Buying Rate</span>
                  </td>
                  <td>
                    <i className="cc LTC" /> Litecoin
                  </td>
                  <td className="text-success">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>
              </tbody>
            </table>
            <Link to="#" style={{float:"right"}}>View More </Link>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
             </div>
        </div>
     </div>
        )
    }
}

export default SupAdminPage
