import React, { Component } from 'react';
import AuthService from "../Services/auth.service";

export class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Users: []
    };

  }
  getUsersData = () => {
    AuthService.getUsers().then(resp => {

      const data = resp.data.data;
      const users = data.map(u =>
        <tr key={u.id}>
          <td>{u.id}</td>
          <td>{u.first_name}</td>
          <td>{u.last_name}</td>
          <td>{u.email}</td>
          <td>{u.status}</td>
        </tr>
      )
      this.setState({
        Users: users
      });
    }).catch(err => {
      console.log(err);
    });
  }
  componentDidMount() {
  
    this.getUsersData();
  }

  render() {
    return (
      <div>
        <div className="content-body">
          <div className="container-fluid">
            <div className="row">
              <div className="col-xl-12">
                <div className="card">
                  <div className="card-header border-0">
                    <h4 className="card-title">Users Records</h4>
                  </div>
                  <div className="card-body pt-0">
                    <div className="transaction-table">
                      <div className="table-responsive">

                        <table id="sortDataTable" className="table table-striped table-bordered table-sm">

                          <thead>
                            <tr>
                              <th>Id</th>
                              <th>First Name</th>
                              <th>Last Name</th>
                              <th>Email</th>
                              <th>Status</th>

                            </tr>
                          </thead>
                          <tbody>
                            {this.state.Users.length>0 ?this.state.Users : 'No Record Found'}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Admin
