import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Sidebar extends Component {
    render() {
        return (
            <div>
<div className="sidebar">
  <div className="menu">
    <ul>
      <li>
        <Link to="/supAdmin" data-toggle="tooltip" data-placement="right" title="Dashboard">
          <span><i className="la la-home" /></span>
        </Link>
      </li>
      <li>
        <Link to="/usersList" data-toggle="tooltip" data-placement="right" title="Users">
          <span><i className="la la-user-alt" /></span>
        </Link>
      </li>
      <li>
        <Link to="/trans" data-toggle="tooltip" data-placement="right" title="Transactions">
          <span><i className="la la-receipt" /></span>
        </Link>
      </li>
     
      <li>
        <Link to="/createUser" data-toggle="tooltip" data-placement="right" title="Admin Users">
          <span><i className="la la-users" /></span>
        </Link>
      </li>
    </ul>
  </div>
</div>

            </div>
        )
    }
}

export default Sidebar
